import React, { useState, useEffect } from "react";
import { NavLink } from "react-router-dom";
import { auth, googleProvider, signInWithPopup, signOut, db } from "./firebase";
import { onAuthStateChanged } from "firebase/auth";
import { doc, setDoc } from "firebase/firestore";
import "./Creator.css";

const Creator = () => {
  const [user, setUser] = useState(null);
  const [isFormVisible, setIsFormVisible] = useState(false);
  const [formData, setFormData] = useState({
    name: "",
    area: "",
    location: "",
    paid: "",
  });

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser || null);
    });
    return () => unsubscribe();
  }, []);

  const handleGoogleLogin = async () => {
    try {
      const result = await signInWithPopup(auth, googleProvider);
      setUser(result.user);

      const userRef = doc(db, "users", result.user.uid);
      await setDoc(
        userRef,
        {
          userId: result.user.uid,
          inviteCode: generateInviteCode(),
          creatorName: result.user.displayName || "Anonymous",
          partyName: "Default Party",
        },
        { merge: true }
      );

      console.log("User logged in and saved: ", result.user);
    } catch (error) {
      console.error("Error signing in with Google: ", error);
    }
  };

  const handleSignOut = async () => {
    try {
      await signOut(auth);
      setUser(null);
      console.log("User signed out");
    } catch (error) {
      console.error("Error signing out: ", error);
    }
  };

  const generateInviteCode = () => {
    return Math.random().toString(36).substring(2, 8).toUpperCase();
  };

  const toggleFormVisibility = () => {
    setIsFormVisible(!isFormVisible);
  };

  const handleFormSubmit = (e) => {
    e.preventDefault();
    console.log("Form Data Submitted: ", formData);
    setFormData({ name: "", area: "", location: "", paid: "" });
    setIsFormVisible(false);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };
  return (
    <div className="creator-container">
      <nav className="navbar">
        <img class="logo" src="https://www.pngplay.com/wp-content/uploads/10/India-Flag-Background-PNG.png" alt="logo" />
        <ul className="nav-links">
          <li>
            <NavLink to="/creator" className={({ isActive }) => (isActive ? "active" : "")}>
              Home
            </NavLink>
          </li>
          <li>
            <NavLink to="/invite" className={({ isActive }) => (isActive ? "active" : "")}>
              Invites
            </NavLink>
          </li>
          <li>
            <NavLink to="/profile" className={({ isActive }) => (isActive ? "active" : "")}>
              Profile
            </NavLink>
          </li>
          <li>
            <NavLink to="/Create_session" className={({ isActive }) => (isActive ? "active" : "")}>
              Create Session
            </NavLink>
          </li>
        </ul>
        {!user ? (
          <div className="google-login-container">
            <button className="google-login-btn" onClick={handleGoogleLogin}>
              <img src="google.png" alt="google-logo" /> Login with Google
            </button>
          </div>
        ) : (
          <div className="google-login-container">
            <button onClick={handleSignOut} className="sign-out-btn">
              <i className="fa-solid fa-right-from-bracket"></i> Sign Out
            </button>
          </div>
        )}
      </nav>

      <div className="content">
        <h1>Welcome to the Creator Page</h1>
        <p>This is the Home section for creators.</p>
      </div>

      {/* Bottom Navbar for Mobile */}
      <nav className="bottom-navbar">
        <NavLink to="/creator" activeClassName="active">
          <i className="fa-solid fa-home"></i> Home
        </NavLink>
        <NavLink to="/invite" activeClassName="active">
          <i className="fa-solid fa-envelope"></i> Invites
        </NavLink>
        <NavLink to="/profile" activeClassName="active">
          <i className="fa-solid fa-user"></i> Profile
        </NavLink>
        <NavLink to="/Create_session" activeClassName="active">
          <i className="fa-solid fa-calendar-plus"></i> Create
        </NavLink>
      </nav>

      <div className="add-icon" onClick={toggleFormVisibility}>
        <i className="fa-regular fa-plus"></i>
      </div>

      {/* Form Popup */}
      {isFormVisible && (
        <div className="form-popup">
          <div className="form-container">
            <div className="cancle">
              <i class="fa-solid fa-circle-xmark" onClick={toggleFormVisibility}></i>
            </div>
            <h2>Add New Entry</h2>
            <form onSubmit={handleFormSubmit}>
              <label>Name:</label>
              <input type="text" name="name" value={formData.name} onChange={handleInputChange} required />
              <label>Area:</label>
              <input type="text" name="area" value={formData.area} onChange={handleInputChange} required />
              <label>Location: </label>
              <input type="text" name="location" value={formData.location} onChange={handleInputChange} required />
              <label> Paid:</label>
              <input type="text" name="paid" value={formData.paid} onChange={handleInputChange} required />
              <button type="submit" className="submit-btn">Submit</button>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default Creator;